@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700; }

.leaflet-container {
  height: 100vh;
  position: absolute !important;
  right: 0;
  width: 100vw; }

.list-popup {
  list-style: none;
  padding-left: 0; }
  .list-popup li {
    display: inline-block;
    text-align: center;
    width: 100%; }
    .list-popup li.title {
      font-size: 18px;
      font-weight: bold; }

.content {
  background: rgba(0, 0, 0, 0.5);
  left: 40px;
  position: absolute;
  text-align: center;
  top: 90px;
  z-index: 1000; }

.header {
  background: #17202A;
  border-bottom: 2px solid #F1C40F;
  height: 60px;
  text-align: center;
  padding: 20px 0 10px 0; }
  .header img {
    width: 120px; }
    @media only screen and (max-width: 768px) {
      .header img {
        float: left;
        margin-left: 20px; } }
  .header .select-box {
    position: absolute;
    right: 20px;
    top: 0;
    width: 180px; }

.header-menu {
  background: #FFF;
  display: none;
  left: 0;
  padding: 0 40px;
  position: fixed;
  top: 0;
  width: calc(100% - 80px);
  z-index: 1; }
  .header-menu ul {
    float: right;
    list-style: none;
    padding-left: 0; }
    .header-menu ul li {
      display: inline-block;
      margin-left: 20px;
      width: auto; }

.list-circuits {
  border: 1px solid #D6DBDF;
  height: calc(100vh - 180px);
  margin: 0 20px;
  overflow-y: scroll;
  padding: 0 40px; }
  .list-circuits ul {
    list-style: none;
    padding: 0;
    text-align: center;
    width: 100%; }
    .list-circuits ul li {
      margin-bottom: 5px;
      padding: 0 10px; }
      .list-circuits ul li a {
        color: #D6DBDF;
        text-decoration: none; }

.select-year {
  width: 150px;
  position: absolute;
  right: 30px;
  top: 30px;
  z-index: 1000; }
  .select-year img {
    margin-bottom: 20px;
    width: 50%; }
  .select-year select {
    width: 100%; }

.select-circuit {
  width: 150px;
  position: absolute;
  right: 30px;
  top: 30px; }
  .select-circuit img {
    margin-bottom: 20px;
    width: 50%; }
  .select-circuit select {
    width: 100%; }

